@import "base";

@keyframes apply {
  from {transform: scale(1.0);}
  to {transform: scale(1.1);}
}


body {
	background-color: #FFE0DE;
	color: #00347A;
	font-size: 20px;
}

a {
	text-decoration: none;
	color: inherit;
}

#wrapper {
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	min-height: 100%;
	width: 100%;
	box-sizing: border-box;
	margin: auto;
	padding: 1em;
	max-width: 1400px;
	font-size: 1.5em;
}

.main-canvas{
	display: flex;
	flex-flow: row wrap;
	justify-content: center;
	height: 100%;
	align-items: center;
	align-content: center;
}
.main-canvas > * {
	position: relative;
	flex: 0 0 auto;
	max-height: 80%;
	width: auto;
	max-width: 40%;
	min-width: 150px;
}
div.logo-container{
	position: relative;
	max-width: 50%;
	min-width: 280px;
	padding-top: 5%;
}

div.logo-container > img {
	height: auto;
	width: 100%;
}
#apply-button{
	right: -10%;
	top: 0;
	position: absolute;
	display:flex;
	padding: 1em;
	background-image: url('/uploads/Asset%202@4x-8.png');
	background-size: 100% 100%;
	width: 7.5vw;
	height: 9.2vw;
	justify-content: center;
	text-align: center;
	align-items: center;
	min-width: 64px;
	min-height: 80px;
	transform-origin: center center;
	animation: apply 1s infinite alternate;
}
.mascot {
	max-height: 70%;
	max-width: 40%;
}
footer.fest-tagline {
	text-align: center;
	width: 100%;
	margin-top: 0.75em;
	max-width: 100%;
}

@media only screen and (max-width: 600px) {
  body {
    font-size: 1em;
  }
  #apply-button {
  	top: -12%;
  	right: -12%;
  }
  .mascot {
  	transform: translateY(10%);
  }
  footer.fest-tagline {
  	margin-top: 0.5em;
  }
}